.catalog {
  min-width: 280px;

  padding: 26px 0;

  border-top: 1px solid @secondary-border-color;
  border-bottom: 1px solid @secondary-border-color;

  @media (min-width: @tablet) {
    padding-top: 59px;
    padding-bottom: 0;

    border-top: none;
    border-bottom: none;
  }

  @media (min-width: @desktop) {
    padding-top: 72px;
  }
}

.catalog__layout {
  display: grid;

  gap: 24px;

  @media (min-width: @tablet) {
    grid-template-columns: repeat(2, 1fr);

    gap: 141px 60px;
  }

  @media (min-width: @desktop) {
    grid-template-columns: repeat(4, 1fr);

    gap: 143px 80px;
  }
}

.catalog__wrapper {
  @media (min-width: @tablet) {
    margin: 0 30px;
    margin-bottom: 68px;
  }

  @media (min-width: @desktop) {
    margin: 0 110px;
    margin-bottom: 76px;
  }
}

.catalog__header {
  margin: 0 20px;

  @media (min-width: @tablet) {
    margin: 0 30px;
  }

  @media (min-width: @desktop) {
    margin: 0 110px;
  }
}

.catalog__title {
  margin-top: 0;
  margin-bottom: 41px;

  .subheading--m;

  @media (min-width: @tablet) {
    margin-bottom: 164px;

    .extra-heading;
  }

  @media (min-width: @desktop) {
    margin-bottom: 165px;
  }
}

.catalog__optional {
  text-align: center;

  border-top: 1px solid @card-border-color;

  @media (min-width: @tablet) {
    border-top: none;
    border: 2px solid @catalog-border-color--additional;
  }
}

.catalog__optional-container {
  margin: 0 20px;

  padding-top: 77px;

  position: relative;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;

  &::before {
    content: "";
    display: block;
    width: 30px;
    height: 2px;
    background-color: @grey-border-color;
    position: absolute;
    top: 39px;

    @media (min-width: @tablet) {
      width: 60px;
      top: 81px;
    }
  }

  &::after {
    content: "";
    display: block;
    width: 30px;
    height: 2px;
    background-color: @grey-border-color;
    position: absolute;
    top: 39px;
    transform: rotate(90deg);

    @media (min-width: @tablet) {
      width: 60px;
      top: 80px;
    }
  }

  @media (min-width: @tablet) {
    margin: 0;
    padding: 161px 36px 38px;

    height: 100%;

    display: flex;
    flex-flow: column nowrap;
  }
}

.catalog__optional-title {
  margin-top: 0;
  margin-bottom: 8px;

  display: inline-block;

  .title--s;

  text-transform: uppercase;

  @media (min-width: @tablet) {
    margin-bottom: 23px;

    .title--s-additional;
  }
}

.catalog__optional-break {
  display: none;

  @media (min-width: @tablet) {
    display: inline-block;
  }
}

.catalog__optional-subtitle {
  margin-top: 0;
  margin-bottom: 0;

  .basic-text-style--xs;

  @media (min-width: @tablet) {
    .basic-text-style--ml-additional;
  }
}

.catalog__optional-button {
  margin-top: 21px;
  padding: 10px 25px;

  min-width: 100%;

  color: @basic-text-color;

  &--bg-grey {
    background-color: @secondary-bg-color;
  }

  @media (min-width: @tablet) {
    margin-top: auto;
  }
}
