.widget {
  min-width: 280px;

  padding: 23px 0 40px;
  padding-top: 25px;

  background-color: @additional-bg-color;

  @media (min-width: @tablet) {
    padding: 90px 0 100px;

    background-image: linear-gradient(@default-bg-color, @default-bg-color 666px, @additional-bg-color 666px, @additional-bg-color);
  }

  @media (min-width: @desktop) {
    padding-top: 0;
    padding-bottom: 68px;

    background-image: linear-gradient(@default-bg-color, @default-bg-color 136px, @additional-bg-color 136px, @additional-bg-color);
  }
}

.widget__wrapper {
  margin: 0 20px;

  @media (min-width: @tablet) {
    margin: 0 30px;
  }

  @media (min-width: @desktop) {
    margin: 0 110px;
  }
}

.widget__layout {
  display: grid;

  gap: 20px;

  @media (min-width: @tablet) {
    gap: 98px;
  }

  @media (min-width: @desktop) {
    grid-template-columns: 1fr 1fr;

    gap: 0 224px;
  }
}

.widget__info-container {
  display: grid;

  gap: 20px;

  @media (min-width: @tablet) {
    grid-template-columns: 2fr 1fr;

    gap: 0;
  }

  @media (min-width: @desktop) {
    grid-template-columns: 436px;
  }
}

.widget__title {
  margin-top: 0;
  margin-bottom: 20px;

  .heading;

  @media (min-width: @tablet) {
    margin-bottom: 63px;

    .extra-heading;
  }

  @media (min-width: @desktop) {
    margin-bottom: 147px;

    .extra-heading;
  }
}

.widget__subtitle {
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: @tablet) {
    grid-column: 1 / -1;

    margin-bottom: 54px;
  }

  @media (min-width: @desktop) {
    max-width: 436px;
    margin-bottom: 69px;
  }
}

.widget__subtitle-break {
  display: none;

  @media (min-width: @tablet) {
    display: inline-block;
  }

  @media (min-width: @desktop) {
    display: none;
  }
}

.widget__info {
  margin: 0;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (min-width: @tablet) {
    justify-content: flex-start;
    gap: 42px;
  }

  @media (min-width: @desktop) {
    margin-bottom: 53px;
  }
}

.widget__info-item {
  display: grid;
  grid-template-rows: repeat(2, min-content);
  justify-content: center;

  gap: 6px;

  position: relative;

  text-align: center;

  @media (min-width: @desktop) {
    gap: 11px;
  }

  @media (min-width: @desktop) {
    gap: 8px;
  }
}

.widget__info-term {
  width: calc(100% - 37px);

  margin-top: -15px;

  justify-self: center;
  order: 2;

  .title--xs;
  font-family: "Arial", "Helvetica", sans-serif;
  text-transform: lowercase;
  background-color: @additional-bg-color;

  @media (min-width: @tablet) {
    width: calc(100% - 19px);

    .basic-text-style--s;
    color: @basic-text-color;
    background-color: @default-bg-color;
  }

  &--width {
    @media (min-width: @tablet) {
      width: calc(100% - 47px);
    }
  }

  @media (min-width: @desktop) {
    width: calc(100% - 20px);

    background-color: @additional-bg-color;
  }
}

.widget__break {
  @media (min-width: @tablet) {
    display: none;
  }
}

.widget__info-def {
  margin-left: 0;
  padding: 13px 20px;
  padding-bottom: 17px;

  .title--m;

  border: 1px solid @additional-border-color;
  border-radius: 3px;

  @media (min-width: @tablet) {
    .title--l;
    padding: 15px 29px;
    padding-bottom: 18px;
  }

  @media (min-width: @desktop) {
    padding-top: 14px;
  }

  &--padding {
    padding: 13px 42px;
    padding-bottom: 17px;

    @media (min-width: @tablet) {
      padding: 15px 56px;
      padding-bottom: 18px;
    }

    @media (min-width: @desktop) {
      padding-top: 14px;
    }
  }
}

.widget__summary {
  .title;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: @tablet) {
    display: flex;
    flex-flow: column nowrap;
    align-self: center;
    gap: 10px;

    padding-bottom: 6px;

    text-align: end;

    .promo-title--tablet;
  }

  @media (min-width: @desktop) {
    text-align: start;
    flex-flow: row wrap;
    gap: 0;
  }
}

.widget__summary-price {
  @media (min-width: @desktop) {
    text-align: start;

    margin-left: 63px;
  }
}

.widget__slider-container {
  margin: 0 auto;
}

.widget__slider-control {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  grid-column: 1 / -1;

  @media (min-width: @desktop) {
    grid-column: 2 / 3;
  }

  &-input {
    appearance: none;
    width: 60%;
    height: 6px;
    background-color: @secondary-border-color;
    outline: none;
    cursor: pointer;
    border-radius: 8px;

    &::-webkit-slider-thumb {
      appearance: none;
      width: 2rem;
      height: 2rem;
      border-radius: 6px;
      background: @default-bg-color;
      border: 1px solid @grey-border-color;
    }
  }

  &-button {
    min-width: 40px;
    padding: 10px 15px;
    background-color: @basic-button-color;
    border: none;
    border-radius: 8px;
    color: @default-text-color;
    cursor: pointer;
    font-size: inherit;
    transition: background-color 0.2s ease-out;

    @media (min-width: @tablet) {
      min-width: 100px;
      padding: 15px 20px;
    }


    &:hover {
      background-color: @basic-button-color--hover;
    }

    &:active {
      background-color: @basic-button-color--active;
    }
  }
}
