.slider {
  width: 280px;
  height: 256px;

  position: relative;

  @media (min-width: @tablet) {
    width: 560px;
    height: 512px;
  }

  &__toggle {
    width: 2px;
    height: 100%;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    background-color: @basic-button-color;
    z-index: 1;
  }
}

.slider__prev {
  position: absolute;
  width: 50%;
  height: 100%;
  left: 0;
}

.slider__after {
  position: absolute;
  height: 100%;
  left: 50%;
}

.slider__image {
  height: 100%;

  object-fit: cover;
}

.slider__prev-image {
  object-position: left;
}

.slider__after-image {
  object-position: right;
}
